							}
						});
					}
				})
			},
			uploadVideoFile() {
				uni.chooseVideo({
					sourceType: ['album', 'camera'],
					success: function(res) {
						self.src = res.tempFilePath;
						console.log(self.src);
						uni.getVideoInfo({
							src: res.tempFilePath,
							success: function(img) {
								self.duration = res.duration;
								self.size = res.size;
								self.height = res.height;
								self.width = res.width;
								console.log(self.duration);
								console.log(img.size);
								console.log(img.height);
								console.log(img.width);
								if ( self.duration>300.0
								) {
							.0
									uni.showToast({
										title: '提交成功',
										duration: 2000
									})
									uni.uploadFile ({
										url: "http://127.0.0.1:5000/uploadvideo", // 后端api接口
										filePath: self.src, // uni.chooseImage函数调用后获取的本地文件路劲
										name: 'file', //后端通过'file'获取上传的文件对象
										formData: {
											// imgurl:res.tempFilePaths[0],

										},
										// header: {
										// 	"Content-Type": "multipart/form-data"
										// },
										success: (res) => {

											console.log(res.data);

										}
									});
								}
								else{
									uni.showToast({
										title: '时长不符要求，提交失败',
										duration: 2000,
										icon:'none',
										
									})
								}
							},
						})



						;

					},
					fail: function(err) {
						console.log(err)
					}

				})
			},


			gotomy() {
				uni.switchTab({
					url:"../my/my"
				})
			},
			getstatus(input) {
				uni.request({
					url: `http://127.0.0.1:5000/users/my?id=${input}`,
					method: "GET",
					success: (res) => {
						console.log(res.data)
						// this.$forceUpdate()
					}
				})
			},
			// onShow() {
			// 	var userInfo = this.getGlobalUser("globalUser")

			// 	if (userInfo != null) {
			// 		// this.userIsLogin = true;
			// 		this.userInfo = userInfo;
			// 	} else {
			// 		// this.userIsLogin = false;
			// 		this.userInfo = {};
			// 	}
			// 	console.log(userInfo.uid);
			// },
		onLoad(options) {
			//this.goods_id = options.id;
			var userInfo = this.getGlobalUser("globalUser")
			if (userInfo != null) {
				this.userIsLogin = true;
				this.userInfo = userInfo;
				this.getstatus(userInfo.uid);
			} else {
				this.userIsLogin = false;
				this.userInfo = {};
			}
		},
